<template>
    <div id="final_wrapper">
          
          <div id="iframe"></div>
          <!-- <iframe src="" frameborder="0"></iframe> -->
          <div class="list"><button>切换资源1</button><button>切换资源2</button><button>切换资源3</button></div>
          <ul id="list">
          
          </ul>
           
    </div> 
</template>

<script>
export default {
    
   mounted(){

       let baseUrl = "http://jx.618g.com/?url=";
       let qiehuan = document.getElementsByClassName("list")[0];
       let this_1 = this;
       qiehuan.addEventListener("click",function(e){
        //    alert(e);
           let target = e.target.innerText.toString().slice(4);
        //    alert(target);
           let arr = ["https://jx.idc126.net/jx/?url=","https://vip.mpos.ren/v/?url=","http://jx.598110.com/?url="];
           this_1.reflash(arr[target-1]);
       },false);
       this.reflash(baseUrl);    
       
    },
    methods:{
       reflash(baseUrl){
           
            let obj = this.$route.params.list;
            // alert(obj.type);
            //                   ("final");
            //                   (obj);
           document.getElementById("iframe").innerHTML = "";
           if(obj.type == "电视剧" || obj.type == "动漫"){    
           let iframeUrl = obj[obj.type+obj.title+"第1集"];
           let iframe = document.createElement("iframe");
           iframe.setAttribute("src",baseUrl+iframeUrl);
           iframe.setAttribute("allowfullscreen","true");
           iframe.setAttribute("mozallowfullscreen","mozallowfullscreen");
           iframe.setAttribute("msallowfullscreen","msallowfullscreen");
           iframe.setAttribute("oallowfullscreen","oallowfullscreen");
           iframe.setAttribute("webkitallowfullscreen","webkitallowfullscreen");  
//  <iframe id="ifrm" allowfullscreen="true" mozallowfullscreen="mozallowfullscreen" msallowfullscreen="msallowfullscreen" oallowfullscreen="oallowfullscreen" webkitallowfullscreen="webkitallowfullscreen"
// 
//        




            

           iframe.style.height = "538px";
           iframe.style.width = "100%";
           document.getElementById("iframe").appendChild(iframe);

              let list = document.getElementById("list");
              if(list.innerHTML == ""){
                    for (const key in obj) {
                        if(key.toString().indexOf(obj["title"]) != -1){
                                                let li = document.createElement("li");
                        //                   (key);
                        //                   (obj[key]);
                        let length = (obj["type"] + obj["title"]).length;
                        li.innerText = key.toString().slice(length+1);
                        li.style.listStyle = "none";
                        li.style.float = "left";
                        li.style.margin = "10px";
                        li.style.border = "1px solid black";
                        li.style.cursor = "pointer";
                        //   li.style.backgroundColor = "white";
                        li.setAttribute('listvalue',obj[key]);
                        //                   (li);
                        document.getElementById("list").appendChild(li);
                        }
                    }
              }
              list.addEventListener("click",function(e){
                //                     (e);
                    for(let i = 0; i < list.children.length;i ++){
                        list.children[i].style.backgroundColor = "white";
                        list.children[i].style.color = "black";
                    }
                    e.target.style.backgroundColor = "black";
                    e.target.style.color = "white";
                    document.getElementById("iframe").innerHTML = "";
                    
                    let iframeUrl = e.target.getAttribute("listvalue");
                    let iframe = document.createElement("iframe");
                    iframe.setAttribute("src",baseUrl+iframeUrl);
                    iframe.setAttribute("allowfullscreen","true");
                    iframe.setAttribute("mozallowfullscreen","mozallowfullscreen");
                    iframe.setAttribute("msallowfullscreen","msallowfullscreen");
                    iframe.setAttribute("oallowfullscreen","oallowfullscreen");
                    iframe.setAttribute("webkitallowfullscreen","webkitallowfullscreen"); 
                    iframe.style.height = "538px";
                    iframe.style.width = "100%";
                    document.getElementById("iframe").appendChild(iframe);
              },false);  
           }else if(obj.type == "电影"){
        //    alert(obj["地址"]);
        //    let baseUrl = "http://jx.598110.com/?url=";
           let iframeUrl = obj["地址"];
           let iframe = document.createElement("iframe");
           iframe.setAttribute("src",baseUrl+iframeUrl);
           iframe.setAttribute("allowfullscreen","true");
           iframe.setAttribute("mozallowfullscreen","mozallowfullscreen");
           iframe.setAttribute("msallowfullscreen","msallowfullscreen");
           iframe.setAttribute("oallowfullscreen","oallowfullscreen");
           iframe.setAttribute("webkitallowfullscreen","webkitallowfullscreen");
           iframe.style.height = "640px";
           iframe.style.width = "1350px";
           document.getElementById("iframe").appendChild(iframe);
        
       }
       },  

       reflash_change(){
           let baseUrl = "https://vip.mpos.ren/v/?url=";
           this.reflash(baseUrl);
       } 
    }
      

   }



</script>

<style scoped>
    li{
        list-style-type: none;
    }
   #reflash{
       position:relative;
       right:0px;
       top:0px;

   }  
   #final_wrapper{
       margin:0px;
       padding:0px;
       display:flex;
       flex-direction:column;
       justify-content: flex-start;
       align-items: center;
       width:1350px;
       height:538px;
   }
    
   #final_wrapper #iframe{
       width:1350px;
       height:640px;   
   } 

</style>